<template>
  <form @submit.prevent="editModule">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">{{ $t('form.edit') }} {{module.name}}</p>
      </header>
      <section class="modal-card-body">
        <b-field :label="$t('form.identifier')" :type="fieldType">
          <b-input v-model="module.identifier" placeholder="Identifier" maxlength="8" disabled></b-input>
        </b-field>
        <b-field :label="$t('form.name')">
          <b-input v-model="module.name" :placeholder="$t('moduleForm.namePlaceholder')" required>
          </b-input>
        </b-field>
        <b-field :label="$t('form.room')">
          <b-autocomplete
              v-model="roomName"
              :placeholder="$t('moduleForm.roomPlaceholder')"
              :data="this.rooms"
              field="name"
              required
              @select="option => module.room = option ? option.id : ''">
          </b-autocomplete>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">{{ $t('form.close') }}</button>
        <button class="button is-primary">{{ $t('form.save') }}</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  import { mapState } from 'vuex'
  export default {
    name: 'editModuleForm',
    props: ['module'],
    computed: {
      ...mapState(['rooms'])
    },
    data () {
      return {
        roomName: this.$store.getters.roomById(this.module.room).name,
        isModuleValid: false,
        loadingModule: false,
        fieldType: ''
      }
    },
    methods: {
      editModule () {
        const loadingComponent = this.$loading.open()

        const moduleUpdated = (response) => {
          this.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
            loadingComponent.close()
            this.$parent.close()
            this.$toast.open({
              message: this.$t('moduleForm.successMessage'),
              type: 'is-success'
            })
          })
        }

        const handleError = (error) => {
          console.log(error)
          loadingComponent.close()
          this.$toast.open({
            message: this.$t('moduleForm.errorMessage'),
            type: 'is-danger'
          })
        }

        let form = {
          name: this.module.name,
          is_active: true,
          room: this.module.room,
          identifier: this.module.identifier,
          module_type: this.module.module_type
        }

        if (!this.module.room) {
          api.addRoom({ name: this.roomName }).then((resolve) => {
            form.room = resolve.data.id
            api.updateModule(this.module.id, form).then(moduleUpdated).catch(handleError)
          }).catch(handleError)
        } else {
          api.updateModule(this.module.id, form).then(moduleUpdated).catch(handleError)
        }
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>



