<template>
  <form @submit.prevent="addAssociate">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">{{ $t('newAssociate.title') }}</p>
      </header>
      <section class="modal-card-body">
        <b-field :label="$t('newAssociate.username')">
          <b-input v-model="associate.username" :placeholder="$t('newAssociate.usernamePlaceholder')" required>
          </b-input>
        </b-field>
        <b-field :label="$t('profile.email')">
          <b-input v-model="associate.email" :placeholder="$t('newAssociate.emailPlaceholder')" required>
          </b-input>
        </b-field>
        <b-field :label="$t('profile.firstname')">
          <b-input v-model="associate.firstName" :placeholder="$t('newAssociate.firstnamePlaceholder')" required>
          </b-input>
        </b-field>
        <b-field :label="$t('profile.lastname')">
          <b-input v-model="associate.lastName" :placeholder="$t('newAssociate.lastnamePlaceholder')" required>
          </b-input>
        </b-field>
        <b-field :label="$t('newAssociate.password')">
          <b-input v-model="associate.password" required type="password" :placeholder="$t('newAssociate.passwordPlaceholder')" password-reveal></b-input>
        </b-field>
        <b-field :label="$t('newAssociate.confirmation')">
          <b-input v-model="associate.confirmation" required type="password" :placeholder="$t('newAssociate.confirmationPlaceholder')" password-reveal></b-input>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">{{$t('form.close')}}</button>
        <button class="button is-primary">{{$t('newAssociate.add')}}</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  // import { mapState } from 'vuex'
  export default {
    name: 'newAssociateForm',
    data () {
      return {
        associate: {
          username: '',
          email: '',
          firstName: '',
          lastName: '',
          password: '',
          confirmation: ''
        }
      }
    },
    methods: {
      addAssociate () {
        const loadingComponent = this.$loading.open()

        const form = {
          username: this.associate.username,
          email: this.associate.email,
          first_name: this.associate.firstName,
          last_name: this.associate.lastName,
          password: this.associate.password
        }

        api.addAssociate(form).then((result) => {
          console.log(result)
          loadingComponent.close()
          this.$parent.close()
          this.$toast.open({
            message: this.$t('newAssociate.successMessage'),
            type: 'is-success'
          })
        }).catch((error) => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>



