<template>
  <form @submit.prevent="editRoom">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">{{ $t('form.edit') }} {{room.name}}</p>
      </header>
      <section class="modal-card-body">
        <b-field :label="$t('form.name')">
          <b-input v-model="room.name" :placeholder="$t('roomForm.namePlaceholder')" required></b-input>
        </b-field>
        <b-field :label="$t('roomForm.description')">
          <b-input v-model="room.description" :placeholder="$t('roomForm.descriptionPlaceholder')">
          </b-input>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">{{ $t('form.close') }}</button>
        <button class="button is-primary">{{ $t('form.save') }}</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  import { mapState } from 'vuex'
  export default {
    name: 'editRoomForm',
    props: ['room'],
    computed: {
      ...mapState(['rooms'])
    },
    methods: {
      editRoom () {
        const loadingComponent = this.$loading.open()

        let form = {
          id: this.room.id,
          name: this.room.name,
          description: this.room.description
        }

        api.updateRoom(form.id, form).then((resolve) => {
          this.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
            loadingComponent.close()
            this.$parent.close()
            this.$toast.open({
              message: this.$t('roomForm.successMessage'),
              type: 'is-success'
            })
          })
        }).catch((error) => {
          console.log(error)
          loadingComponent.close()
          this.$toast.open({
            message: this.$t('roomForm.errorMessage'),
            type: 'is-danger'
          })
        })
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>



