<template>
  <b-table
    :data="associates"
    :bordered="true"
    :striped="false"
    :narrowed="false"
    :hoverable="false"
    :mobile-cards="true">

    <template scope="props">
      <b-table-column :label="$t('profile.firstname')">
        {{ props.row.first_name }}
      </b-table-column>

      <b-table-column :label="$t('profile.lastname')">
        {{ props.row.last_name }}
      </b-table-column>

      <b-table-column :label="$t('profile.username')">
        {{ props.row.username }}
      </b-table-column>

      <b-table-column :label="$t('profile.email')">
        {{ props.row.email }}
      </b-table-column>

      <b-table-column :label="$t('associatesTable.actions')">
        {{$t('associatesTable.actionsPlaceholder')}}
      </b-table-column>
    </template>

    <template slot="empty">
        <section class="section">
            <div class="content has-text-grey has-text-centered">
                <p>{{$t('associatesTable.message')}}</p>
            </div>
        </section>
    </template>
  </b-table>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'associatesTable',
    props: ['associates'],
    computed: {
      ...mapState(['rooms'])
    },
    methods: {
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>


